/*================================
//  Pagination [component]
=================================*/

.pagination-wrapper {
    margin-top: 50px;
    // display: inline-block;
    margin-bottom: 25px;
    @include media-only('md', min) {
        margin-bottom: 0;
    }
    ul.pagination {
        .active {
            background: #0289FF;
            color: $color-white;
        }
        gap: 5px;
        li {
            a {
                font-family: $font-roboto;
                font-weight: 500;
                @include toRem(font-size, 16);
                line-height: 19px;
                color: $color-title;
                background: #F7F7F7;
                border-radius: 5px;
                border: none;
                padding: 8px 13px;
                &:hover {
                    background: #0289FF;
                    color: $color-white;
                }
            }
        }
    }
}